"use strict";
/// <reference types="jquery" />
/// <reference path="../types/jquery/index.d.ts"/>
/// <reference path="./browser.d.ts"/>
/// <reference path="./util.ts"/>
$(function () {
    var $button = $('#mobile-menu-button');
    var $menu = $('#mobile-menu');
    var $body = $('body');
    var prepared = false;
    var visible = false;
    var close = function () {
        $menu.hide(0, function () {
            visible = false;
            $body.css({ position: 'static' });
        });
    };
    $button.click(function () {
        if (visible) {
            close();
        }
        else {
            $body.css({ position: 'fixed' });
            $menu.show(0, prepare);
        }
    });
    /**
     * Wire menu events
     */
    function prepare() {
        visible = true;
        if (prepared)
            return;
        $menu.find('.close').click(close);
        $menu.find('.menu-categories').on('change', 'select', function (e) {
            close();
            window.location.assign($(e.target).val());
        });
        prepared = true;
    }
});
